
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Card from 'vux/src/components/card/index.vue'

import {httpUrl} from '../../http_url';

export default {
directives: {
TransferDom
},
data () {
return {
menus: {
menu1: '退出登录'
},
showMenus: false,
product_data:'',
orderList:[],
length:'',
payMoney:[],
repayMoneySum:'',
all_price:0
}
},
created(){
this.getData();
},
components: {
XHeader,
PopupHeader,
Actionsheet,
Card ,
FormPreview,
},
methods:{
getData(){
const _this = this;
this.$axios({
method: 'post',
url:httpUrl.billList
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.all_price = res.data.retData.all_price;
this.orderList = res.data.retData.list;
this.length = res.data.retData.list.length;
for(var index in this.orderList){
_this.payMoney.push(this.orderList[index].order_pre_price);
}
this.repayMoneySum = this.moneySum(_this.payMoney);
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
moneySum(arr){
var s = 0;
arr.forEach(function(val, index, arr) {
s += val;
}, 0);
return s;
},
LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}

}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
goHome(){
this.$router.push('/home');
},
goRepaymentRecord(){
this.$router.push('/repayment_record');
}
}
}



